import { useState } from "react";
import { useNetwork, useContractWrite, useAccount } from 'wagmi';
import { parseEther } from "viem";
import { addressesNFT, pricesNFT } from "../config";
import abi from "../abis/nft.json";

function Minting() {
    const { address, isConnected } = useAccount()
    const { chain } = useNetwork()
    const [count, setCount] = useState(0);
    const { write } = useContractWrite({
        address: addressContract(),
        abi: abi,
        functionName: 'mintNFTs',
        args: [count], 
        value: msgValue()
      })

    function msgValue() {
        const ID: number = isConnected? chain?.id as number : 5;
        const price: number = pricesNFT[ID]
        const amount: string = (price * count).toString();
        return parseEther(amount)
    }

    function addressContract(): `0x${string}` | undefined {
        const ID: number = chain?.id as number;
        return addressesNFT[ID] || addressesNFT[5]
    }
      
        return (
          <>
              <div>Count:</div>
              <input value={count} onChange={(e) => {setCount(Number(e.target.value))}} type="number" />
              {address}
              <button
                disabled={!write || !isConnected}
                onClick={() =>
                  write()
                }
              >
                Mint
              </button>
          </>
        );
  }
  
  export default Minting;